.back-to-top {
	position: fixed;
	bottom: 40px;
	right: 30px;
	font-size: 100px;
	background: #0095e8;
	color: white;
	opacity: 0.5;
	cursor: pointer;
	border-radius: 100px;
	border: none;
	box-shadow: 0 5px 10px #ccc;
}

.back-to-top:hover {
	background: #0b9eec;
	opacity: 1;
}

.back-to-top {
	width: 40px;
	height: 40px;
	border-radius: 11px;
}

.arrow {
	position: absolute;
	bottom: 8px;
	right: 8px;
	height: 25px;
	width: 25px;
}
